#main-nav-links {
  display: flex;
}

.hamburger-tab {
  color: black !important;
  text-decoration: none;
  font-size: 20px;
}

.tabpanel .dropdown-item a {
  color: black;
  text-decoration: none;
  padding: 10px;
}

.dropdown {
  padding: 0px !important;
}

.dropdown-menu {
  background-color: #e9e0a9 !important;
}

.dropdown-item a{
  text-decoration: none;
  color: black;
  font-size: 18px;
  padding: 10px;
}

.nav-item a:hover {
  color: rgb(112, 80, 21);
  transition: 0.3s ease-in-out;
}

#main-nav-links {
  align-items: center;
}

#main-nav-links .tab {
  color: #e9e0a9 !important;
  text-decoration: none;
  padding: 30px;
  font-size: 25px;
  font-weight: lighter;
}

#main-nav-links .nav-link {
  color: #e9e0a9 !important;
  text-decoration: none;
}

#main-nav-links .tab:hover {
  color: rgb(112, 80, 21) !important;
  transition: 0.3s ease-in-out;
}

#main-nav-links .nav-link:hover {
  color: rgb(112, 80, 21) !important;
  transition: 0.3s ease-in-out;
}

/* Extra small devices */
@media only screen and (min-width: 320px) and (max-width: 479px) {


  /* NAV */

  #topBar {
    overflow: hidden;
    flex-direction: row;
  }

  #topBar img {
    padding-bottom: 10px;
    height: 35%;
    width: 35%;
  }

  #main-nav-links {
    flex-wrap: wrap;
    justify-content: center;
    display: none !important;
  }

  #hamburger-btn {
   display: contents !important;
   font-size: 40px !important;
   color: #e9e0a9 !important;

  }


}

/* Portrait tablets and large phones, 600px + */
@media only screen and (min-width: 480px) and (max-width: 768px) {

  #topBar {
    overflow: hidden;
  }

  #topBar img {
    width: 25%;
    height: 25%;
    padding: 10px;
  }

  #topBar img {
    width: 25%;
    height: 25%;
    padding: 10px;
  }

  #main-nav-links {
    padding-bottom: 10px;
  }

  #main-nav-links .tab {
    font-size: 18px;
    padding: 5px;
  }
  
}

/* Landscape tablets, 768px + */
@media only screen and (min-width: 769px) and (max-width: 979px) {

  #topBar img {
    width: 25%;
    height: 25%;
    padding-top: 25px;
  }
}
